// Dependencies
import React, { Fragment } from 'react'
import API from '../component/index.json'

// Component
import Sidenav from '../component/sidenav'

// Imagenes

// Page
class Index extends React.Component {
    
    darkMode(e) {
        console.log(e.target)
    }

    componentDidMount() {
        const btn = document.getElementById('btn-menu')
        btn.addEventListener('click',(e) => {
            switch (e.target.children[0].textContent) {
                case "menu":
                        e.target.children[0].textContent = 'close'
                        document.getElementById('sidenav').style.transform = 'translateX(0px)'
                    break;
                    case "close":
                        e.target.children[0].textContent = 'menu'
                    break;
                default:
                        e.target.children[0].textContent = 'menu'
                    break;
            }
        })
    }
    
    render() {
        return <Fragment>
                <Sidenav section="geo" />
                <main className="main-index">
                    <section className="section-article">
                        <h1>Aula 1</h1>
                        <div className="aula-2">
                            {API.aula1.map(e => (
                            <article key={e.id} className="elements">
                                <div key={e.id + 100}>
                                <img loading="lazy" key={e.id+200} src={e.url} alt="error" />
                                </div>
                                <p key={e.id+300}>{e.description}</p>
                            </article>
                            ))}
                        </div>
                    </section>
                    <section className="section-article">
                        <h1>Aula 2</h1>
                        <div className="aula-2">
                            {API.aula2.map(e => (
                            <article key={e.id} className="elements">
                                <div key={e.id+500}>
                                <img loading="lazy" key={e.id + 600} src={e.url} alt="error" />
                                </div>
                            <p key={e.id+700}>{e.description}</p>
                            </article>
                            ))}
                        </div>
                    </section>
                </main>
        </Fragment>
    }
}

export default Index;